.text-center {
  text-align: center; }

/** Media Queries **/
:root {
  --fuentePrincipal: 'Baloo Tammudu 2', cursive;
  --primario: #75b7c0;
  --boton: #3e828b;
  --blanco: #ffffff;
  --gris: #383838;
  --negro: #000000; }

*, *:before, *:after {
  box-sizing: inherit; }

html {
  font-size: 62.5%;
  box-sizing: border-box; }

body {
  font-family: var(--fuentePrincipal);
  font-size: 2rem;
  line-height: 2; }

h1 {
  font-family: "Poppins", sans-serif;
  font-size: 2rem;
  text-align: center;
  color: var(--negro);
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 2rem;
  margin: 0 0 0 2rem; }

button {
  color: var(--negro);
  font-family: var(--fuentePrincipal);
  background-color: var(--blanco);
  border: none;
  text-decoration: none;
  text-transform: uppercase;
  font-weight: 700;
  letter-spacing: 2rem;
  animation: pulso 3s ease-in-out; }

@keyframes pulso {
  0% {
    transform: translateX(600px); }
  to {
    transform: translateX(10px); } }

a {
  text-decoration: none;
  cursor: default; }

p {
  margin: 0;
  line-height: 2rem; }

svg {
  cursor: pointer; }

.no-scroll {
  overflow: hidden; }

.contenedor {
  height: 100vh; }

.medio {
  height: 85%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin: 0; }

.inicio {
  height: 100%; }

.imagen {
  background: url("../../build/img/fondoWeb.png");
  background-size: contain;
  background-repeat: no-repeat;
  background-position: center center;
  width: 100%;
  height: 100%; }
  @media (min-width: 480px) {
    .imagen {
      background: url("../../build/img/fondoWebTelefono.png");
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center center;
      width: 100%;
      height: 100%; } }
  @media (min-width: 768px) {
    .imagen {
      background: url("../../build/img/fondoWebTablet.png");
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center center;
      width: 100%;
      height: 100%; } }
  @media (min-width: 1024px) {
    .imagen {
      background-image: url("../../build/img/fondoWebDesktop.png");
      background-size: contain;
      background-repeat: no-repeat;
      background-position: center center;
      width: 100%;
      height: 100%; } }

.portfolio {
  max-width: 85%;
  margin: 0 auto;
  margin-top: 2rem; }

.contenedor-salir {
  transform: rotate(90deg);
  margin-left: -7rem;
  position: fixed; }
  @media (min-width: 480px) {
    .contenedor-salir {
      margin-left: -5rem; } }
  @media (min-width: 768px) {
    .contenedor-salir {
      position: relative; } }

.contenedor-contacto {
  transform: rotate(-90deg);
  margin-right: -12rem;
  margin-left: -5rem;
  margin-top: -3rem;
  position: fixed;
  right: 0; }
  @media (min-width: 480px) {
    .contenedor-contacto {
      margin-right: -10rem; } }
  @media (min-width: 768px) {
    .contenedor-contacto {
      position: relative; } }

.contenido-proyecto {
  padding-bottom: 3rem; }
  @media (min-width: 768px) {
    .contenido-proyecto {
      padding-bottom: 1rem; } }

.imgProyecto {
  width: 100%; }

@media (min-width: 768px) {
  .grid {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr); } }

@media (min-width: 1024px) {
  .grid {
    display: grid;
    gap: 3rem;
    grid-template-columns: repeat(3, 1fr);
    row-gap: 4rem; } }

.grid a {
  cursor: pointer; }

.proyecto {
  text-align: center;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  margin-left: 2rem; }

.botones-proyecto {
  display: flex;
  justify-content: flex-end;
  height: 3rem;
  column-gap: 3px;
  margin-top: -1rem; }

.orden-proyecto {
  display: flex;
  justify-content: space-between; }

.repo {
  color: var(--blanco);
  background-color: var(--negro);
  border-radius: 4px;
  width: 50%;
  text-align: center;
  font-weight: 500;
  transition: background-color .3s ease; }

.app {
  background-color: var(--primario);
  color: var(--blanco);
  border-radius: 4px;
  width: 50%;
  text-align: center;
  font-weight: 500;
  transition: background-color .3s ease; }

.app:hover {
  cursor: pointer;
  background-color: var(--boton); }

.repo:hover {
  cursor: pointer;
  background-color: var(--gris); }

.boton {
  background-color: var(--primario);
  border: none;
  color: var(--blanco);
  border-radius: 5px;
  font-weight: bold;
  padding: 1rem 1rem 0;
  transition: background-color .3s ease; }

.boton:hover {
  cursor: pointer;
  background-color: var(--boton); }

.contenido {
  margin: 0 auto;
  height: 100%;
  width: 100%; }
  @media (max-width: 768px) {
    .contenido {
      width: 80%;
      margin-top: 2rem; } }

.seccion {
  display: none; }

.ocultar {
  display: none; }

.mostrar-seccion {
  display: block !important; }

.contacto {
  height: 100%; }

p span {
  font-size: 2.5rem; }

.icono-li {
  align-items: bottom; }

.contenido-contacto {
  text-align: center;
  width: 100%;
  height: 50%;
  padding: 5rem 0 9rem;
  margin: 0 auto; }
  @media (min-width: 768px) {
    .contenido-contacto {
      width: 50%; } }

.formulario {
  background-color: var(--blanco);
  width: 90%;
  height: 100%;
  margin: 0 auto; }
  @media (min-width: 480px) {
    .formulario {
      width: 70%; } }
  @media (min-width: 768px) {
    .formulario {
      width: 60%; } }
  @media (min-width: 1024px) {
    .formulario {
      width: 50%; } }

.campo {
  width: 100%; }

.campo__field {
  border-radius: 5px;
  margin: 10px 0;
  background: #fff;
  border: 1px solid #ccc;
  color: #777;
  width: 100%;
  padding: 7px 8px; }

.campo__field {
  cursor: pointer; }

input:hover {
  border-width: 2px;
  border-color: var(--primario); }

textarea:hover {
  border-width: 2px;
  border-color: var(--primario); }

.campo__field--textarea {
  border-radius: 5px;
  margin: 10px 0;
  background: #fff;
  border: 1px solid #ccc;
  color: #777;
  max-width: 100%;
  outline: none;
  padding: 7px 8px; }
